/* Navigation.css */


.sidebar {
    position: fixed;
    top: 55px;
    left: -300px;
    width: 300px;
    height: 100%;
    background-color:#fffffe;
    /*outline-color: #c4e9fff0;*/
    transition: left 0.3s ease;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* サイドバーに影を追加 */
    overflow-y: auto;
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .sidebar-content {
    padding: 0px;
    overflow: auto; /* または overflow: scroll; */
  }
  
  button.close-button {
    top: 16px;
    right: 16px;
    background-color: #363636;
    color: #fff;
    padding: 8px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
  }
  
  button.toggle-button {
    font-family: 'Courier New', monospace;
    position: fixed;
    font-size: 20px;
    top: 29px;
    left: 16px;
    transform: translateY(-50%);
    background-color: #58aaff;
    color: #ffffff;
    padding: 2px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
    white-space: nowrap;
    z-index: 2;
  }
  
  .Title-text {
    font-family: 'Courier New', monospace;
    position: fixed;
    top: 30px;
    left: 55px;
    font-size: 20px;
    transform: translateY(-50%);
    margin-left: 8px;
    writing-mode: horizontal-tb;
    white-space: nowrap; /* テキストが横に改行されないようにする */
    z-index: 2;
    overflow: hidden;
  }

  .Title-Lesson-text {
    font-family: 'Courier New', monospace;
    position: fixed;
    top: 30px;
    right: 3%;
    font-size: 20px;
    transform: translateY(-50%);
    writing-mode: horizontal-tb;
    white-space: nowrap; /* テキストが横に改行されないようにする */
    z-index: 2;
    text-align: left;
    overflow: hidden;
}

