  /* サイドバー内のアイテムスタイル */
 /* サイドバー全体 */
  ul {
    list-style-type: none;
    padding-top: 8px;
    margin: 0;
  }
  
  /* リスト項目 */
  li {
    font-family: 'Courier New', monospace;
    padding-top: 4px;
    padding-bottom: 6px;
    padding-left: 0px;
    border-bottom: 1px solid #c4e9fff0;
    text-align: left;
  }
  
  /* テキストリンク */
  li button {
    font-size: 16px;
    border: none;
    outline-offset: 4px; /* アウトラインからボタンのコンテンツまでの距離 */
    text-align: left; /* ボタンを左揃えにする */
    transition: background-color 0.3s ease; /* 背景色変更のアニメーション効果を追加 */
    background-color: #00000000;
  }
  
  /* マウスオーバー時のスタイル */
  li button:hover {
    color: #006698;
    
  }
  
  /* クリック時のスタイル */
  li button:active {
    background-color: #8f8f8f00;
  }

  li:last-child {
    border-bottom: none;
  }
  