.common-style {
    /* 共通のスタイルを定義 */

    body {
      font-family: 'Courier New', monospace;
      background-color: #f8f9fa;
      color: #343a40;
      margin: 0;
      padding: 0;
    }
    
    .common-style {
      max-width: 90%;
      margin: 0 auto;
      padding: 20px;
      text-align: left;
      /*overflow-y: auto;*/
    }
    
    h1, h2, h3 {
      color: #006698;
    }
    
    /* リンク */
    a {
      color: #007bff;
      text-decoration: none;
    }
    
    a:hover {
      text-decoration: underline;
    }
    
    /* 段落 */
    p {
      text-align: left;
      margin-bottom: 15px;
    }
  }